export const getTokenPair = (chainId, params) => {
	if (!chainId) {
		return;
	}

	if (params) {
		const tokenA = params['tokenA']
		const tokenB = params['tokenB']

		if (tokenA && tokenB && tokenA !== tokenB) {
			return {
				tokenA,
				tokenB
			}
		}
	}

	return DefaultTokenPair[chainId]
}

