import React from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import NavBar from "../components/nav/NavBar";
import { getERC20Info, ChainExplorer } from "../api/dapp";
import { post } from "../api/rest";

class Report extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            pair: props.pair,
            symbolA: '',
            symbolB: '',
            chainId: null,
            explorer: null,
            txHash: '',
            address: null
        }
    }

    async onConnect(res, props) {

        const tokenAinfo = await getERC20Info(res.provider, this.state.pair.tokenA, res.address)
        const tokenBinfo = await getERC20Info(res.provider, this.state.pair.tokenB, res.address)
        this.adddress = res.address

        this.setState({
            symbolA: tokenAinfo.symbol,
            symbolB: tokenBinfo.symbol,
            chainId: res.chainId,
            explorer: ChainExplorer[res.chainId]
        })
    }

    // on textarea change , set txHash
    onTxHashChange(e) {
        this.setState({
            txHash: e.target.value
        })
    }

    // on paste buttom press, set the txHash
    onPaste() {
        navigator.clipboard.readText().then(text => {
            this.setState({
                txHash: text
            })
        })
    }

    doReport() {
        // check the txHash is valid
        if (!this.state.txHash) {
            alert('Please input the transaction hash')
            return
        }
        // check the txHash is valid by regex 
        const reg = new RegExp(/^(0x)?[0-9a-fA-F]{64}$/)
        if (!reg.test(this.state.txHash)) {
            alert('Invalid transaction hash')
            return
        }
        // check the chainId not null
        if (!this.state.chainId) {
            alert('Please connect wallet')
            return
        }
        // post to /api/v1/tick/fix
        post('/api/v1/tick/fix', {
            chainId: this.state.chainId,
            hash: this.state.txHash
        }).then(res => {
            if (res.code === 0) {
                alert('Report success')
            } else {
                alert(res.msg)
            }
        }).catch(err => {
            console.log(err)
            alert('Report fail')
        })
        
    }

    render() {
        return (
            <div>
                <NavBar onConnect={(res) => { this.onConnect(res, this.props) }} tokenA={this.state.pair.tokenA} tokenB={this.state.pair.tokenB} />
                <div style={{ padding: '16px' }}>
                    <div style={{ fontWeight: 'bold' }}>
                        If you post an order that does not show up in the market, follow these steps to fix the problem
                    </div>
                    <div style={{ marginTop: '20px' }}>
                        <div>1  <a style={{ color: '#0B57FF' }} href={this.state.explorer + '/address/' + this.state.adddress} target="_blank">CLICK HERE</a> to open Blockchain browser</div>
                        <div>2 Locate the transaction you issued the order to, copy the transaction hash, paste it into the input box below, and click the OK button.</div>
                    </div>
                    {/* an textarea input for input the transaction hash */}
                    <div style={{ marginTop: '20px' }}>

                        <div style={{ display: 'flex' }}>
                            <textarea onChange={(e) => {this.onTxHashChange(e)}} value={this.state.txHash} style={{ width: '100%', height: '100px', borderRadius: '8px', padding: '10px' }} maxLength={66} placeholder="Please input the transaction hash"></textarea>
                            <div style={{
                                borderRadius: '8px',
                                marginLeft: '10px',
                                width: '90px',
                                backgroundColor: '#ffffff',
                                color: '#0B57FF',
                                textAlign: 'center',
                                verticalAlign: 'middle',
                                alignItems: 'center',
                                justifyItems: 'center',
                                lineHeight: '100px',
                            }}
                                onClick={() => { this.onPaste() }   }
                            >
                                Paste
                            </div>
                        </div>
                        <div style={{ marginTop: '36px', fontSize: '16px' }}>
                            <button  style={{ padding: '16px', borderRadius: '8px', backgroundColor: '#0b57ff', color: '#ffffff', width: '100%' }}>Report</button>
                        </div>
                    </div>
                </div>
            </div>
        )
    }

}

function ReportPage() {
    const [params] = useSearchParams();
    const navigate = useNavigate();

    const pair = {
        tokenA: params.get("tokenA"),
        tokenB: params.get("tokenB"),
        tickId: params.get("tickId")
    }

    return (
        <div>
            <Report pair={pair} />
        </div>
    )
}

export default ReportPage;