import React from "react";

import NavBar from "../components/nav/NavBar";
import { getTokenPair, conn, connect, getERC20Info, ContractMapping, ABI_TICK } from "../api/dapp";
import { Link } from 'react-router-dom'
import { ethers } from 'ethers';
import { get } from "../api/rest";
import { useLocalStorage } from '../util/storage';
//
// import { tab } from "@testing-library/user-event/dist/tab";

class Market extends React.Component {

    constructor(props) {
        super(props)
        console.log("Market props: ", props)
        this.state = {
            tokenA: null,
            tokenB: null,
            address: null,
            provider: null,
            chainId: null,
            tab: 0,

            symbolA: null,
            decimalsA: null,
            balanceOfA: null,
            frozenBalanceOfA: null,

            symbolB: null,
            decimalsB: null,
            balanceOfB: null,
            frozenBalanceOfB: null,

            items: [],
            nomore: false,
            loading: false,
            page: 0,
            size: 10,

            showMenu: false,
            showWithdraw: false

        }
        conn().then(res => {
            const pair = getTokenPair(res.chainId, props)
            this.setState({
                tokenA: pair.tokenA,
                tokenB: pair.tokenB,
            })
        })
    }

    init = async (res) => {
        // get tokenA info
        const tokenAInfo = await getERC20Info(res.provider, this.state.tokenA)
        // get tokenB info
        const tokenBInfo = await getERC20Info(res.provider, this.state.tokenB)
        this.setState({
            symbolB: tokenBInfo.symbol,
            decimalsB: tokenBInfo.decimals,
            balanceOfB: tokenBInfo.balance,


            symbolA: tokenAInfo.symbol,
            decimalsA: tokenAInfo.decimals,
            balanceOfA: tokenAInfo.balance,
        })

        // get the balance of tokenB in marketContract
        const marketContract = new ethers.Contract(ContractMapping[res.chainId], ABI_TICK, res.provider)
        // balanceOfA
        const balanceOfA = await marketContract.getBalance(this.state.tokenA, res.address)
        const frozenBalanceOfA = await marketContract.getFrozen(this.state.tokenA, res.address)

        const balanceOfB = await marketContract.getBalance(this.state.tokenB, res.address)
        const frozenBalanceOfB = await marketContract.getFrozen(this.state.tokenB, res.address)

        this.setState({
            balanceOfB: balanceOfB,
            frozenBalanceOfB: frozenBalanceOfB,
            balanceOfA: balanceOfA,
            frozenBalanceOfA: frozenBalanceOfA,
        })

        this._loadData();
    }

    onConnect(res) {
        this.setState({
            provider: res.provider,
            address: res.address,
            chainId: res.chainId,
        });

        this.init(res)
    }

    onAccountChange(accounts) {
        this.setState({
            address: accounts[0]
        });
    }

    onChainSwitch(chainId) {

        if (chainId !== this.state.chainId) {
            const res = getTokenPair(this.state.chainId)
            this.setState({
                tokenA: res.tokenA,
                tokenB: res.tokenB
            });
            // eslint-disable-next-line
            const [tokenA, setTokenA] = useLocalStorage('tokenA', res.tokenA);
            // eslint-disable-next-line
            const [tokenB, setTokenB] = useLocalStorage('tokenB', res.tokenB);
        }

        this.setState({
            chainId: chainId
        });
    }

    doWithdraw = async (token, amount) => {
        try {
            const res = await connect()
            const signer = await res.provider.getSigner()
            const marketContract = new ethers.Contract(ContractMapping[this.state.chainId], ABI_TICK, signer)
            const tx = await marketContract.withdraw(token, amount)
            console.log("doWithdraw tx: ", tx)
            await tx.wait(1)
            this.init(res)
        } catch (e) {
            alert(e.reason)
        }
    }

    switchTab(_tab) {
        this.setState({
            tab: _tab
        }, () => {
            this._reload();
        });

    }

    _loadData = async () => {
        if (this.state.loading) {
            return;
        }

        this.setState({
            loading: true
        });

        let tokenA = this.state.tokenA;
        let tokenB = this.state.tokenB;

        if (this.state.tab === 1) {
            tokenA = this.state.tokenB;
            tokenB = this.state.tokenA;
        }

        let params = {
            tokenA: tokenA,
            tokenB: tokenB,
            page: this.state.page,
            size: this.state.size
        }

        if (this.state.tab === 2) {
            // mine
            params['txFrom'] = this.state.address;
        }

        const { code, data } = await get('/api/v1/tick/market', params)
        if (code === 1) {
            this.setState({
                loading: false,
                items: [...this.state.items, ...data.content],
                nomore: data.content.length === 0
            });
        }
    }

    _loadMore = async () => {
        if (this.state.nomore) {
            return;
        }
        this.setState({
            page: this.state.page + 1
        }, () => {
            this._loadData();
        });
    }

    _reload = async () => {
        this.setState({
            page: 0,
            items: [],
            nomore: false,
            loading: false
        }, () => {
            this._loadData();
        });
    }

    render() {

        const tabActiveStyle = {
            fontSize: '18px',
            color: '#000000',
            marginRight: '20px',
            fontStyle: 'bold'
        }
        const tabStyle = {
            color: 'rgba(0, 0, 0, 0.6)',
            marginRight: '20px',
        }



        //  a bottom Menu with cover mask
        const withdrawMenu = <div style={{
            position: 'absolute',
            bottom: '0px',
            left: '0px',
            width: '100%',
            height: '100%',
            backgroundColor: 'rgba(0,0,0,0.5)',
            zIndex: '100',
            justifyContent: 'center',
            display: 'flex',
            alignItems: 'center'
        }} onClick={(e) => {
            // hide menu
            this.setState({
                showWithdraw: false
            });
        }}>
            {/* the bottom menu */}
            <div style={{
                width: '90%',
                backgroundColor: 'rgba(255,255,255,0.95)',
                borderRadius: '8px',
                padding: '12px',
                fontSize: '14px',
                position: 'absolute',
                bottom: '30px',
                boxShadow: '0px 0px 8px 0px rgba(0,0,0,0.1)',
            }}>
                {/* 3 items 'Buy', 'Sell', 'Not found my order'? */}
                <div style={{ lineHeight: '60px' }}
                    onClick={(e) => {
                        this.doWithdraw(this.state.tokenA, this.state.balanceOfA);
                    }}
                >
                    <div style={{ fontSize: '14pt', fontWeight: 'bold' }}>Withdraw {this.state.symbolA}</div>
                </div>
                <div style={{ lineHeight: '60px' }}
                    onClick={(e) => {
                        this.doWithdraw(this.state.tokenB, this.state.balanceOfB);
                    }}
                >
                    <div style={{ fontSize: '14pt', fontWeight: 'bold' }}>Withdraw {this.state.symbolB}</div>
                </div>
            </div>
        </div>

        const myCard =
            <div style={{

            }}>
                <div style={{ color: '#ffffff', display: 'flex', justifyContent: 'space-between', margin: '16px', alignItems: 'center', borderRadius: '8px', border: '0', padding: '12px', backgroundColor: '#0B57FF', fontSize: '14px' }}>
                    <div style={{ display: 'flex', alignItems: 'start', lineHeight: '2' }}>
                        <div style={{ marginRight: '16px', textAlign: 'left' }}>
                            {/* <div>Total balance: {ethers.formatUnits(this.state.balanceOfB ? this.state.balanceOfB + '' : '0', this.state.decimalsB)} {this.state.symbolB}</div> */}
                            <div>Available  <span style={{ fontWeight: 'bold', fontSize: '16px' }}>{ethers.formatUnits(this.state.balanceOfB ? (this.state.balanceOfB - this.state.frozenBalanceOfB) + '' : '0', this.state.decimalsB)} {this.state.symbolB}</span> </div>
                            {/* <div>Frozen:  {ethers.formatUnits(this.state.frozenBalanceOfB?this.state.frozenBalanceOfB+'':'0', this.state.decimalsB)} {this.state.symbolB}</div> */}
                            <div>Available  <span style={{ fontWeight: 'bold', fontSize: '16px' }}>{ethers.formatUnits(this.state.balanceOfA ? (this.state.balanceOfA - this.state.frozenBalanceOfA) + '' : '0', this.state.decimalsA)} {this.state.symbolA} </span></div>
                        </div>
                    </div>
                    <div>
                        <div onClick={
                            (e) => {
                                // this.doWithdraw(this.state.tokenB, this.state.balanceOfB); 
                                this.setState({
                                    showWithdraw: true
                                });
                            }
                        } style={{ marginRight: '12px', color: '#0b57ff', backgroundColor: '#ffffff', padding: '6px 6px 6px 6px', borderRadius: '20px' }}>
                            Withdraw
                        </div>
                    </div>
                </div>
                {/* <div style={{ display: 'flex', justifyContent: 'space-between', margin: '16px', alignItems: 'center', borderRadius: '8px', border: '1px solid #0b57ff', padding: '12px', backgroundColor: '#ffffff', fontSize: '12px' }}>
                    <div style={{ display: 'flex', alignItems: 'start' }}>
                        <div style={{ marginRight: '16px' }}>
                            <div>Total balance: {ethers.formatUnits(this.state.balanceOfA ? this.state.balanceOfA + '' : '0', this.state.decimalsA)} {this.state.symbolA}</div>
                            <div>Available balance:  {ethers.formatUnits(this.state.balanceOfA ? (this.state.balanceOfA - this.state.frozenBalanceOfA) + '' : '0', this.state.decimalsA)} {this.state.symbolA} </div>
                            <div>Frozen:  {ethers.formatUnits(this.state.frozenBalanceOfA?this.state.frozenBalanceOfA+'':'0', this.state.decimalsA)} {this.state.symbolA}</div>
                        </div>
                    </div>
                    <div>
                        <div
                            onClick={
                                (e) => { this.doWithdraw(this.state.tokenA, this.state.balanceOfA); }
                            }
                            style={{ marginRight: '16px', color: '#0b57ff' }}>
                            Withdraw
                        </div>
                    </div>
                </div> */}
            </div>



        //  a bottom Menu with cover mask
        const bottomMenu = <div style={{
            position: 'absolute',
            bottom: '0px',
            left: '0px',
            width: '100%',
            height: '100%',
            backgroundColor: 'rgba(0,0,0,0.5)',
            zIndex: '100',
            justifyContent: 'center',
            display: 'flex',
            alignItems: 'center'
        }} onClick={(e) => {
            // hide menu
            this.setState({
                showMenu: false
            });
        }}>
            {/* the bottom menu */}
            <div style={{
                width: '90%',
                backgroundColor: 'rgba(255,255,255,0.95)',
                borderRadius: '8px',
                padding: '12px',
                fontSize: '14px',
                position: 'absolute',
                bottom: '30px',
                boxShadow: '0px 0px 8px 0px rgba(0,0,0,0.1)',
            }}>
                {/* 3 items 'Buy', 'Sell', 'Not found my order'? */}
                <div style={{ lineHeight: '60px' }}>
                    <Link to={{
                        pathname: '/addtick',
                        search: '?tokenA=' + this.state.tokenA + '&tokenB=' + this.state.tokenB + '&direction=buy',
                    }}  >
                        <div style={{ fontSize: '14pt', fontWeight: 'bold' }}>Buy {this.state.symbolA}</div>
                    </Link>
                </div>
                <div style={{ lineHeight: '60px' }}>
                    <Link to={{
                        pathname: '/addtick',
                        search: '?tokenA=' + this.state.tokenA + '&tokenB=' + this.state.tokenB + '&direction=sell',
                    }}  >
                        <div style={{ fontSize: '14pt', fontWeight: 'bold' }}>Sell {this.state.symbolA}</div>
                    </Link>
                </div>
                <div style={{ lineHeight: '60px' }}>
                    <Link to={{
                        pathname: '/report',
                        search: '?tokenA=' + this.state.tokenA + '&tokenB=' + this.state.tokenB,
                    }}  >
                    <div style={{ fontSize: '14pt', fontWeight: 'bold' }}>Not listed?</div>
                    </Link>
                </div>

            </div>
        </div>

        return (

            <div>
                <NavBar
                    tokenA={this.state.tokenA}
                    tokenB={this.state.tokenB}
                    onConnect={this.onConnect.bind(this)}
                    onChainSwitch={this.onChainSwitch.bind(this)}
                    onAccountChange={this.onAccountChange.bind(this)}
                />
                <div style={{
                    height:
                        '0px'
                }}></div>
                <div style={{ display: 'flex', justifyContent: 'space-between', margin: '16px', alignItems: 'center' }}>
                    <div style={{ display: 'flex', alignItems: 'center' }}>
                        <div onClick={(e) => { this.switchTab(0) }} style={this.state.tab === 0 ? tabActiveStyle : tabStyle}>Sell</div>
                        <div onClick={(e) => { this.switchTab(1) }} style={this.state.tab === 1 ? tabActiveStyle : tabStyle}>Buy</div>
                        <div onClick={(e) => { this.switchTab(2) }} style={this.state.tab === 2 ? tabActiveStyle : tabStyle}>Mine</div>
                    </div>
                    <div onClick={(e) => {
                        // show menu
                        this.setState({
                            showMenu: true
                        })
                    }}>
                        {/* <Link to={{
                            pathname: '/addtick',
                            search: '?tokenA=' + this.state.tokenA + '&tokenB=' + this.state.tokenB,
                        }}  > */}
                        <img alt='' style={{ width: '20px', height: '20px' }} src={require('../assets/plus.png')} />
                        {/* </Link> */}
                    </div>
                </div>

                {/* if tab == 2 display the myCard */}
                {this.state.tab === 2 ? myCard : null}

                {/* iterator the this.state.items with MarketItem component */}
                {
                    this.state.items.length > 0 ?
                        this.state.items.map((item, index) => {
                            return <MarketItem key={index} item={item} tab={this.state.tab} />
                        }) :

                        <div style={{
                            display: 'flex',
                            justifyContent: 'center',
                            alignItems: 'center',
                            height: '200px',
                            width: '80%',
                            margin: 'auto',
                            marginTop: '160px',
                            borderRadius: '8px',
                            padding: '12px',
                        }}>
                            {/* in the center of the screen, as a card */}
                            {/* <center  >Empty records <br />
                                <Link to={{
                                    pathname: '/addtick',
                                    search: '?tokenA=' + this.state.tokenA + '&tokenB=' + this.state.tokenB,
                                }}  >
                                    <span style={{ color: '#0b57ff' }}>Place an order</span>
                                </Link>
                            </center> */}
                            <center>
                                <img alt="" src={require('../assets/empty.png')} />
                            </center>
                        </div>
                }

                {this.state.showMenu ? bottomMenu : null}
                {this.state.showWithdraw ? withdrawMenu : null}
            </div>
        );
    }
}

class MarketItem extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            show: true
        }
    }

    doCancel = async () => {
        const item = this.props.item

        if (!window.confirm("Are you sure to cancel this order?")) {
            return;
        }
        try {
            const res = await connect()
            const signer = await res.provider.getSigner()
            const marketContract = new ethers.Contract(ContractMapping[item.chainId], ABI_TICK, signer)
            const tx = await marketContract.cancelTick(item.tokenAContract, item.tokenBContract, item.tickId)
            console.log("doCancel tx: ", tx)
            // await tx.wait(1)

            this.setState({
                show: false
            });
        } catch (e) {
            alert(e.reason)
        }
    }

    render() {
        const item = this.props.item
        const sellTag = <div style={{ backgroundColor: '#FE696C', color: '#ffffff', borderRadius: '4px', padding: '2px 4px', marginRight: '6px' }}>SELL</div>
        const buyTag = <div style={{ backgroundColor: '#04BF85', color: '#ffffff', borderRadius: '4px', padding: '2px 4px', marginRight: '6px' }}>BUY</div>

        const buyButton =
            <Link to={{
                pathname: '/bid',

                search: '?tokenA=' + item.tokenAContract + '&tokenB=' + item.tokenBContract + '&tickId=' + item.tickId,
            }}>
                <div style={{ fontSize: '12pt', backgroundColor: '#04BF85', color: '#ffffff', borderRadius: '24px', padding: '6px 14px', marginRight: '6px' }}>BUY</div>
            </Link>
        const sellButton =
            <Link to={{
                pathname: '/bid',
                search: '?tokenA=' + item.tokenAContract + '&tokenB=' + item.tokenBContract + '&tickId=' + item.tickId,
            }}>
                <div style={{ fontSize: '12pt', backgroundColor: '#FE696C', color: '#ffffff', borderRadius: '24px', padding: '6px 14px', marginRight: '6px' }}>SELL</div>
            </Link>

        const selfCancelButton = item.filled < item.tokenAAmount ?
            <div onClick={(e) => this.doCancel()} style={{ fontSize: '12pt', backgroundColor: '#ffffff', color: 'blue', border: '1px solid blue', borderRadius: '24px', padding: '6px 14px', marginRight: '6px' }}>Cancel</div> : null


        return (
            this.state.show ? <div style={{
                display: 'flex', justifyContent: 'space-between', margin: '16px', alignItems: 'center', borderRadius: '8px', border: '0px ', padding: '12px', backgroundColor: '#ffffff', fontSize: '12px'
            }}>
                <div style={{
                    textAlign
                        : 'left'
                }}>
                    <div style={{
                        display: 'flex',
                        alignItems: 'center'
                    }}>
                        {
                            this.props.tab === 0 ? sellTag : this.props.tab === 1 ? buyTag : null
                        }
                        <div
                            style={{ fontSize: '14pt', fontWeight: 'bold' }}
                        >
                            {this.props.item.price} {this.props.item.tokenBSymbol}/{this.props.item.tokenASymbol}
                        </div>
                    </div>
                    <div style={{ height: '8px' }}></div>
                    <div>
                        Available {this.props.item.tokenAAmount - this.props.item.filled} {this.props.item.tokenASymbol} / Total  {this.props.item.tokenAAmount} {this.props.item.tokenASymbol}
                    </div>
                </div>
                <div>
                    {/* button to trade */}
                    {/* <div style={{ marginRight: '16px', color: '#0b57ff' }}> */}

                    {/* <Link to={{ pathname: '/trade', query: { tokenA: this.state.tokenA, tokenB: this.state.tokenB } }}>Trade</Link> */}
                    {/* </div> */}
                    {
                        this.props.tab === 0 ? buyButton : this.props.tab === 1 ? sellButton : selfCancelButton
                    }

                </div>

            </div> : null
        )
    }
}

export default Market;