import React from 'react';

import { watchTxRecp, conn } from '../api/dapp.js'
import { Link } from 'react-router-dom'
import { getStorageValue } from '../util/storage.js';

import { useLocation } from 'react-router-dom';
import { ethers } from 'ethers';
import TLoading from '../components/common/TLoading.js';

class TransactionResult extends React.Component {

    constructor(props) {
        super(props);
        // props.txHash is Transaction hash
        this.state = {
            txStatus: 0, // 0 is process 1 is success 2 is failed
            tx: null,
            txFrom: 'Pending...',
            txTo: 'Pending...',
            txValue: 'Pending...',
            txGas: 'Pending...',
            txGasPrice: 'Pending...',
            txNonce: 'Pending...',
            txData: 'Pending...',
            txBlockNumber: 'Pending...',

            tokenA: null,
            tokenB: null,

            loaded: false
        }

        this.timer = null
        this.load()
    }

    async load() {

        const tokenA = getStorageValue('tokenA')
        const tokenB = getStorageValue('tokenB')
        this.setState({
            tokenA: tokenA,
            tokenB: tokenB
        })

        const res = await conn()
        const _this = this

        watchTxRecp(_this.timer, {
            provider: res.provider,
            txHash: _this.props.txHash,

            success: (recp) => {
                _this.setState({
                    txStatus: 1,
                    tx: recp,
                    txFrom: recp.from,
                    txTo: recp.to,
                    txValue: recp.value,
                    txGas: recp.gasUsed,
                    txGasPrice: recp.gasPrice,
                    
                    txData: recp.data,
                    txBlockNumber: recp.blockNumber,
                    loaded: true
                })
            },

            failed: (recp) => {
                _this.setState({
                    txStatus: 2,
                    tx: recp,
                    txFrom: recp.from,
                    txTo: recp.to,
                    txValue: recp.value,
                    txGas: recp.gasUsed,
                    txGasPrice: recp.gasPrice,
                    txData: recp.data,
                    txBlockNumber: recp.blockNumber,
                    loaded: true

                })
            },

            process: () => {
                _this.setState({
                    tx_status: 0
                })
            },

            error: (error) => {
                console.error(error)
                _this.setState({
                    tx_status: 2
                })
            }

        })
    }

    render() {

        const imgStyle = {
            width: '100px',
            height: '100px'
        }

        const rowStyle = {
            display: 'flex',
            justifyContent: 'space-between',
        }

        const rowKeyStyle = {
            color: '#999999'
        }

        const rowValueStyle = {
            color: '#333333'
        }

        const waitingIcon = <div>
            <img alt='' style={imgStyle} src={require('../assets/icon-wait.png')} />
            <div>Transaction is processing...</div>
        </div>

        const successIcon = <div>
            <img alt='' style={imgStyle} src={require('../assets/icon-success.png')} />
            <div>Transaction is success</div>
        </div>

        const failedIcon = <div>
            <img alt='' style={imgStyle} src={require('../assets/icon-fail.png')} />
            <div>Transaction is failed</div>
        </div>

        const boardStyle = {
            margin: '16px',
        }

        return (
            this.state.loaded ?
                <div style={boardStyle}>
                    
                    <center style={{marginTop: '40px', marginBottom:'10px', lineHeight: '60px'}}>
                        {this.state.txStatus === 0 ? waitingIcon : this.state.txStatus === 1 ? successIcon : failedIcon}
                    </center>

                    <div style={{
                        backgroundColor: '#ffffff',
                        padding: '16px',
                        borderRadius: '8px',
                        boxShadow: '0px 0px 8px 0px rgba(0,0,0,0.1)',
                        lineHeight:'2'
                    }}>
                        <div style={rowStyle}>
                            <div style={rowKeyStyle}>Hash: </div>
                            <div style={rowValueStyle} >
                                <div style={{display:'flex', alignItems: 'center'}}>
                                    <div style={{marginRight:'6px'}}>{this.props.txHash.replace(/^(.{10}).+(.{8})$/, "$1...$2")}</div>
                                    <img src={require('../assets/copy.png')} style={{width:'20px', height:'20px'}}></img>
                                </div>
                            </div>
                        </div>

                        <div style={rowStyle}>
                            <div style={rowKeyStyle}>From: </div>
                            <div style={rowValueStyle} >{this.state.txFrom.replace(/^(.{6}).+(.{4})$/, "$1...$2")}</div>
                        </div>

                        <div style={rowStyle}>
                            <div style={rowKeyStyle}>To: </div>
                            <div style={rowValueStyle} >{this.state.txTo.replace(/^(.{6}).+(.{4})$/, "$1...$2")}</div>
                        </div>

                        <div style={rowStyle}>
                            <div style={rowKeyStyle}>Transaction Value: </div>
                            <div style={rowValueStyle} >{this.state.txValue > 0 ? ethers.formatEther(this.state.txValue) : 'Not applicable'}</div>
                        </div>

                        <div style={rowStyle}>
                            <div style={rowKeyStyle}>Gas used: </div>
                            <div style={rowValueStyle} >{this.state.txGas + ''}</div>
                        </div>

                        <div style={rowStyle}>
                            <div style={rowKeyStyle}>Gas Price: </div>
                            <div style={rowValueStyle} >{ethers.formatEther(this.state.txGasPrice)}</div>
                        </div>

                        <div style={rowStyle}>
                            <div style={rowKeyStyle}>Gas cost: </div>
                            <div style={rowValueStyle} >{ethers.formatEther(this.state.txGasPrice) * (this.state.txGas + '')}</div>
                        </div>

                        <div style={rowStyle}>
                            <div style={rowKeyStyle}>Block Number: </div>
                            <div style={rowValueStyle} >{this.state.txBlockNumber}</div>
                        </div>
                    </div>

                    {/** a button click to return /market */}
                    <Link to={{
                        pathname: '/market',
                        // search: '?tokenA=' + this.state.tokenA + '&tokenB=' + this.state.tokenB,
                    }} >
                        {/* a large blue button to Return  */}
                        <div style={{
                            backgroundColor: '#1E90FF',
                            color: '#ffffff',
                            padding: '8px',
                            borderRadius: '50px',
                            boxShadow: '0px 0px 8px 0px rgba(0,0,0,0.1)',
                            lineHeight:'2',
                            marginTop: '30px',
                            marginBottom: '16px',
                            marginLeft: '30px',
                            marginRight: '30px',
                            textAlign: 'center'
                        }}>
                            Return
                        </div>
                    </Link>

                </div> : <div>
                    <TLoading show={!this.state.loaded} />
                </div>
        );
    }
}

function TransactionResultPage() {
    let location = useLocation()
    console.log(location.state)
    return (
        <div>
            <TransactionResult txHash={location.state.txHash} />
        </div>
    )
}

export default TransactionResultPage;