import React from "react";

import { useSearchParams } from 'react-router-dom'
import { ContractMapping, ABI_TICK, getERC20Info, getERC20Allowance, connect, ABI_ERC20 } from '../api/dapp.js';
import NavBar from "../components/nav/NavBar";
import { ethers } from 'ethers';
import { useNavigate } from 'react-router-dom';
// import { push } from 'react-router-redux';
import TLoading from "../components/common/TLoading";
// import {navTo} from "../util/history";

class BidTick extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            pair: props.pair,

            symbolA: 'Loading...',
            symbolB: 'Loading...',

            decimalsA: 0,
            decimalsB: 0,

            balanceA: 0,
            balanceB: 0,

            tickPrice: null,
            tickTotalAmount: null,
            tickFilledAmount: null,
            tickSaller: null,

            amount: 0,
            platformFeeRate: 0,
            marketFeeRate: 0,

            allowanceB: 0,

            loading: false
        }
    }

    showLoading() {
        this.setState({
            loading: true
        })
    }

    hideLoading() {
        this.setState({
            loading: false
        })
    }

    onConnect = (res, props) => {
        this.init(res, props)
    }

    onAmountChange = async (e) => {
        this.setState({
            amount: e.target.value
        })

    }

    init = async (res, props) => {

        // log the stat pair info
        const tokenAinfo = await getERC20Info(res.provider, this.state.pair.tokenA, res.address)
        const tokenBinfo = await getERC20Info(res.provider, this.state.pair.tokenB, res.address)

        const marketContract = ContractMapping[res.chainId]
        const tickContract = new ethers.Contract(marketContract, ABI_TICK, res.provider)
        const tick = await tickContract.getTickById(this.state.pair.tokenA, this.state.pair.tokenB, this.state.pair.tickId)

        console.log('Price ', tick[0])

        // get the platform fee and the sub fee
        const platformFeeRate = await tickContract.getFeeRate()
        const marketFeeRate = await tickContract.getSubFeeRate(props.pair.tokenA, props.pair.tokenB)

        // get the allowance of tokenB
        const allowanceB = await getERC20Allowance(res.provider, this.state.pair.tokenB, res.address, marketContract)


        this.setState({

            symbolA: tokenAinfo.symbol,
            symbolB: tokenBinfo.symbol,

            decimalsA: tokenAinfo.decimals,
            decimalsB: tokenBinfo.decimals,

            balanceA: tokenAinfo.balance,
            balanceB: tokenBinfo.balance,

            tickPrice: tick[0],
            tickTotalAmount: tick[1],
            tickFilledAmount: tick[2],
            tickSaller: tick[5],

            platformFeeRate: platformFeeRate,
            marketFeeRate: marketFeeRate,

            allowanceB: allowanceB,
        })
    }

    doBuy = async (e) => {
        // parse the amount
        const amount = ethers.parseUnits(this.state.amount + '', this.state.decimalsB)
        // check the allowance of tokenB
        if (this.state.allowanceB < amount) {
            alert('Please approve the tokenB first')
            return
        }
        // how many tokenB should be paid
        // const tokenBAmount = amount * (this.state.tickPrice).div(ethers.BigNumber.from(10).pow(this.state.decimalsA))
        try {
            this.showLoading()
            // const tokenBAmount = amount * this.state.tickPrice / (10 ** this.state.decimalsA);
            const tokenAAmount = ethers.parseUnits(this.state.amount / ethers.formatUnits(this.state.tickPrice, this.state.decimalsB) + '', this.state.decimalsA)
            // log tokenA, tokenB, tickId, amount
            console.log(this.state.pair.tokenA, this.state.pair.tokenB, this.state.pair.tickId, tokenAAmount)

            const res = await connect()
            const provider = res.provider
            const signer = await provider.getSigner()
            const contract = new ethers.Contract(ContractMapping[res.chainId], ABI_TICK, signer)

            const tx = await contract.buy(this.state.pair.tokenA, this.state.pair.tokenB, this.state.pair.tickId, tokenAAmount)
            console.log(tx)

            console.log('to push: ', tx.hash);
            // const navigate = useNavigate();
            // navigate('/result', {})
            
            if (typeof this.props.onBidSuccess === 'function') {
                this.props.onBidSuccess({
                    txHash: tx.hash,
                    // tokenA: this.state.tokenA,
                    // tokenB: this.state.tokenB,
                    // direction: this.state.direction,
                    // price: this.state.price,
                    // amount: this.state.amount,
                    // platformFee: this.state.platformFee,
                    // marketFee: this.state.marketFee,
                    // total: this.state.total,
                    // totalPlatformFee: this.state.totalPlatformFee,
                    // totalMarketFee: this.state.totalMarketFee,
                })
            }
        } catch (e) {
            console.log(e)
            alert('ERR: ' + e.reason)
        } finally {
            this.hideLoading()
        }
    }

    doApprove = async (e, tokenContract, amountInUint256) => {

        console.log("doApprove: ", tokenContract, amountInUint256)

        try {
            this.showLoading()
            const res = await connect()
            const provider = res.provider
            const signer = await provider.getSigner()
            const contract = new ethers.Contract(tokenContract, ABI_ERC20, signer)

            if (res.chainId + '' === '202008201' && tokenContract.toLocaleLowerCase() === '0x950259F74FC297A44bc3725aBa2E9B9028B2A53a'.toLocaleLowerCase() && amountInUint256 > 0) {
                // get the et allowance first
                const etContract = new ethers.Contract('0x950259F74FC297A44bc3725aBa2E9B9028B2A53a', ABI_ERC20, signer)
                const etAllowance = await etContract.allowance(res.address, ContractMapping[res.chainId])
                // if etAllowance > 0 then set amountInUnit256 = 0
                if (etAllowance > 0) {
                    amountInUint256 = 0
                }
            }

            const tx = await contract.approve(ContractMapping[res.chainId], amountInUint256)

            console.log("tx: ", tx)
            await tx.wait(1)

            // this._initAllowance(res, this.props)
            this.init(res, this.props)
        } catch (e) {
            console.log(e)
            alert('ERR: ' + e.reason)
        } finally {
            this.hideLoading()
        }
    }

    setMax = async (e) => {
        const l1 = ethers.formatUnits(this.state.balanceB + '', this.state.decimalsB);
        // 4000ET
        console.log('l1 ', l1)

        //  100 ET * 0.01
        const l2 = ethers.formatUnits((this.state.tickTotalAmount - this.state.tickFilledAmount) + "", this.state.decimalsA) * ethers.formatUnits(this.state.tickPrice + '', this.state.decimalsB);

        console.log('tick price', this.state.tickPrice)
        console.log('l2 ', l2)
        // 100ET

        const res = parseFloat(l1) > parseFloat(l2) ? l2 : l1

        console.log('res ', res)

        this.setState({
            amount: res
        })
    }

    render() {

        let remains = ethers.formatUnits((this.state.tickTotalAmount ? (this.state.tickTotalAmount - this.state.tickFilledAmount) : 0) + '', this.state.decimalsA);
        const cardStyle = {
            padding: '16px',
            margin: '12px',
            borderRadius: '8px',
            backgroundImage: `url(${require('../assets/bid-bg-2.png')})`,
            height: '220px',
            backgroundSize: '100% 100%',
            color: '#ffffff',
            position: 'relative',
        }

        let error1 = (<div></div>)
        if (this.state.amount && this.state.allowanceB - ethers.parseUnits(this.state.amount + '', this.state.decimalsB) < 0) {
            error1 = (

                <div style={{ fontSize: '12px', color: 'red' }}>Insufficient allowance

                    <span onClick={(e) => { this.doApprove(e, this.state.pair.tokenB, ethers.parseUnits(this.state.amount + '', this.state.decimalsB)) }} style={{ color: 'blue' }}>[Click to Approve]</span>
                </div>
            )
        } else {
            error1 = (<div></div>)
        }

        return (
            <div>
                <NavBar onConnect={(res) => { this.onConnect(res, this.props) }} tokenA={this.state.tokenA} tokenB={this.state.tokenB} />

                <div>
                    <div style={cardStyle}>
                        <div style={{
                            fontStyle: 'bold', fontSize: '18pt', marginTop: '22px', textAlign: 'center'
                        }} >{ethers.formatUnits((this.state.tickPrice ? this.state.tickPrice : 0) + '', this.state.decimalsB)} {this.state.symbolB} per {this.state.symbolA}</div>
                        <div
                            style={{ marginTop: '12px', textAlign: 'center', fontSize: '12pt' }}
                        >{remains} {this.state.symbolA} Remains</div>

                        <div style={{ position: 'absolute', bottom: '50px', left: '16px', right: '16px', display: 'flex', justifyContent: 'space-between', alignItems: 'center', color: '#cccccc', fontSize: '12px' }}>
                            <div>Seller: {this.state.tickSaller ? this.state.tickSaller.replace(/^(.{6}).+(.{4})$/, "$1...$2") : ''}</div>
                            <div>ID: #{this.state.pair.tickId}</div>
                        </div>
                    </div>

                </div>

                <div style={{ margin: '12px' }}>
                    <div style={{ marginTop: '16px', fontSize: '12px', display: 'flex', justifyContent: 'space-between' }}>
                        <div>Amount</div>
                        <div>
                            <span style={{ fontSize: '12px', color: '#999999' }}>Balance: {ethers.formatUnits(this.state.balanceB + '', this.state.decimalsB)} {this.state.symbolB} </span>
                        </div>
                    </div>

                    <div style={{ marginTop: '5px', display: 'flex', alignItems: 'center' }}>
                        <input onChange={(e) => this.onAmountChange(e)} value={this.state.amount} style={{ width: '100%', padding: '12px', borderRadius: '8px', }} placeholder="Amount" type="number" max={remains}></input>
                        <div style={{
                            display: 'flex', backgroundColor: '#ffffff', alignItems: 'center'
                        }}>
                            <div onClick={(e) => { this.setMax() }} style={{ paddingRight: '10px', color: '#0b57ff', fontSize: '14px' }}>
                                Max
                            </div>
                            <div
                                style={{ padding: '12px', borderRadius: '0 8px 8px 0', backgroundColor: '#CCCCCC' }}
                            >   {this.state.symbolB}
                            </div>
                        </div>
                    </div>
                    <div style={{ marginTop: '5px' }}>
                        <div style={{ fontSize: '12px', color: '#999999' }}>Allowance quotas: {ethers.formatUnits(this.state.allowanceB + '', this.state.decimalsB)}  {this.state.symbolB} </div>
                        {error1}
                    </div>
                    {/* <div style={{ marginTop: '16px', fontSize: '12px' }}>
                        Fee rate
                    </div>
                    <div style={{ marginTop: '5px', fontSize: '12px' }}>
                        <div>
                            <i>Platform fee:{ ethers.parseUnits(this.state.amount, this.state.decimalsB) } ,{parseFloat(this.state.platformFeeRate + '') / 100}% </i>
                        </div>
                        <div>
                            <i>Market fee:,{parseFloat(this.state.marketFeeRate + '') / 100}% </i>
                        </div>
                    </div> */}

                    {/** the totle token B required or received */}
                    <div style={{ marginTop: '16px', fontSize: '12px' }}>
                        Will received
                    </div>
                    <div style={{ marginTop: '5px', fontSize: '16px', fontStyle: 'bold' }}>
                        <div>
                            <i>{this.state.amount && this.state.tickPrice ? this.state.amount / ethers.formatUnits(this.state.tickPrice, this.state.decimalsB) : '-'} {this.state.symbolA}</i>
                        </div>
                    </div>

                    {/** button to place the order */}
                    <div style={{ marginTop: '36px', fontSize: '16px' }}>
                        <button onClick={this.doBuy} style={{ padding: '16px', borderRadius: '8px', backgroundColor: '#0b57ff', color: '#ffffff', width: '100%' }}>Bid</button>
                    </div>
                </div>
                <TLoading show={this.state.loading} />
            </div>
        );
    }
}




function BidTickPage() {
    const [params] = useSearchParams();
    const navigate = useNavigate();

    const pair = {
        tokenA: params.get("tokenA"),
        tokenB: params.get("tokenB"),
        tickId: params.get("tickId")
    }

    return (
        <BidTick pair={pair} onBidSuccess={
            (res) => {
                
                navigate('/result', { state: res })
            }
        }/>
    );
}

export default BidTickPage;