import React from "react";
import Connect from "./Connect";
import Language from './Lang';
// import getIcon from '../../api/dapp';
import { getIcon } from '../../api/dapp';

class NavBar extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            address: props.address,
            chainId: props.chainId,
            blocknumber: props.blocknumber,
            provider: null,

            onConnect: props.onConnect,
            onChainSwitch: props.onChainSwitch,
            onAccountChange: props.onAccountChange,
            onBlockNumberChange: props.onBlockNumberChange
        }
    }

    onConnect(res) {
        this.setState({
            provider: res.provider,
            address: res.address,
            chainId: res.chainId,
            blocknumber: res.blocknumber
        });
        if (this.props.onConnect) {
            this.props.onConnect(res);
        }
    }
    onBlockNumberChange(block) {
        this.setState({
            blocknumber: block
        });
        if (this.props.onBlockNumberChange) {
            this.props.onBlockNumberChange(block);
        }

    }
    onAccountChange(accounts) {
        this.setState({
            address: accounts[0]
        });
        if (this.props.onAccountChange) {
            this.props.onAccountChange(accounts);
        }
    }
    onChainSwitch(chainId) {
        this.setState({
            chainId: chainId
        });
        if (this.props.onChainSwitch) {
            this.props.onChainSwitch(chainId);
        }
    }
    render() {
        const navbarWrapper = {
            display: "flex",
            justifyContent: "space-between",
            padding: "16px",
            alignItems: 'center'
        }


        return (
            <div style={navbarWrapper}>
                <div style={{
                    display: "flex",
                    alignItems: 'center'
                }}>
                    <div style={{
                        display: 'flex'
                    }}>
                        <img src={getIcon(this.props.tokenA)} style={{
                            width: '20px',
                            height: '20px',
                            marginRight: '-4px',
                            zIndex: '9999'
                        }} />   <img src={getIcon(this.props.tokenB)} style={{ 
                            width: '20px',
                            height: '20px',
                            // marginLeft: '8px',
                        }} />
                    </div>
                    <img src={require('../../assets/arrow-down-filling.png')} style={{
                        width: '14px',
                        height: '14px',
                        marginLeft: '8px',
                    }} />
                </div>
                <div style={
                    { display: "flex",alignItems: 'center' }
                }>
                    <Connect
                        onAccountChange={(accounts) => this.onAccountChange(accounts)}
                        onBlockNumberChange={(block) => this.onBlockNumberChange(block)}
                        onChainSwitch={(chainId) => this.onChainSwitch(chainId)}
                        onConnect={(res) => this.onConnect(res)}
                    />
                    <div style={{
                        width
                            : '10px'
                    }}></div>
                    <Language />
                </div>
            </div>
        );
    }
}

export default NavBar;