import React from "react";
import { useSearchParams } from "react-router-dom";
import NavBar from "../components/nav/NavBar";
import { useNavigate } from 'react-router-dom';

import {
    getERC20Info,
    ABI_TICK,
    ContractMapping,
    connect,
    getERC20Allowance,
    ABI_ERC20,
    getFrozen,
    getBalance,

} from "../api/dapp";

import { ethers } from 'ethers';
import TLoading from "../components/common/TLoading";

class MakeTick extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            tokenA: props.pair.tokenA,
            tokenB: props.pair.tokenB,
            symbolA: 'Loading...',
            symbolB: 'Loading...',
            amountA: 'Loading...',
            amountB: 'Loading...',
            decimalsA: -1,
            decimalsB: -1,

            direction: props.pair.direciton,

            price: 0,
            amount: 0,
            platformFee: 0,
            marketFee: 0,
            total: '-',

            totalPlatformFee: '-',
            totalMarketFee: '-',

            allowanceBalanceA: 0,
            allowanceBalanceB: 0,

            balanceOfA: 0,
            balanceOfB: 0,

            loading: false
        };
    }

    doAddTick = async (e) => {

        if (this.state.price <= 0) {
            alert("Price should be greater than zero")
            return
        }

        if (this.state.amount <= 0) {
            alert("Amount should be greater than zero")
            return
        }

        if (this.state.decimalsA < 0 || this.state.decimalsB < 0) {
            alert("Please wait for loading")
            return
        }

        let tx = null;
        if (this.state.direction === 'buy') {
            tx = await this._doBuy()
        }
        else {
            tx = await this._doSell()
        }

        console.log('to push');

        if (typeof this.props.onBidSuccess === 'function') {
            this.props.onBidSuccess({
                txHash: tx.hash,
            })
        }
    }

    async _doSell() {
        if (this.state.amount && this.state.direction === 'sell' && this.state.allowanceBalanceA - ethers.parseUnits(this.state.amount + '', this.state.decimalsA) - this.state.balanceOfA < 0) {
            alert("Insufficient balance or allowance, Please approve first")
            return
        }

        const res = await connect()
        const provider = res.provider
        const signer = await provider.getSigner()
        const contract = new ethers.Contract(ContractMapping[res.chainId], ABI_TICK, signer)
        console.log("SELL|contract address: ",
            ContractMapping[res.chainId],
            "singer", signer.address,
            "tokenA", this.state.tokenA,
            "tokenB", this.state.tokenB,
            "price", ethers.parseUnits(this.state.price.toString(), this.state.decimalsB),
            "amount", ethers.parseUnits(this.state.amount.toString(), this.state.decimalsA))


        const tx = await contract.addTick(
            this.state.tokenA,
            this.state.tokenB,
            ethers.parseUnits(this.state.price.toString(), this.state.decimalsB),
            ethers.parseUnits(this.state.amount.toString(), this.state.decimalsA),
            // {
            //     gasLimit: CustomGasForListTick[res.chainId].gasLimit,
            //     gasPrice: CustomGasForListTick[res.chainId].gasPrice
            // }
        )
        
        return tx;
    }

    async _doBuy() {
        if (this.state.amount && this.state.direction === 'buy' && this.state.allowanceBalanceB - ethers.parseUnits((this.state.price * this.state.amount) + '', this.state.decimalsB) - this.state.balanceOfB < 0) {
            // console.log("allowanceBalanceB", this.state.allowanceBalanceB)
            // console.log("amount", ethers.parseUnits(this.state.amount + '', this.state.decimalsB))

            // console.log("result", this.state.allowanceBalanceB - ethers.parseUnits(this.state.amount + '', this.state.decimalsB) - this.state.balanceOfB    )
            alert("Insufficient balance or allowance, please click 'Approve' first")
            return
        }

        const res = await connect()
        const provider = res.provider
        const signer = await provider.getSigner()
        const contract = new ethers.Contract(ContractMapping[res.chainId], ABI_TICK, signer)

        // sell tokenB, buy tokenA
        // real price
        console.log(ethers.parseUnits(this.state.amount.toString(), this.state.decimalsA))
        console.log(ethers.parseUnits(this.state.price.toString(), this.state.decimalsB))

        const _tokenA = this.state.tokenB
        const _tokenB = this.state.tokenA

        const _decimalA = this.state.decimalsB
        const _decimalB = this.state.decimalsA

        const _amount = ethers.parseUnits((this.state.amount * this.state.price).toString(), _decimalA)
        const _price = ethers.parseUnits((1 / this.state.price).toString(), _decimalB)

        // alert(_price);


        // const _price =
        //     ethers.parseUnits(this.state.amount.toString(), this.state.decimalsA)
        //         / (ethers.parseUnits(this.state.price.toString(), this.state.decimalsB))

        console.log("BUY|contract address: ",
            ContractMapping[res.chainId],
            "singer", signer.address,
            "tokenA", _tokenA,
            "tokenB", _tokenB,
            "price", _price,
            "amount", _amount)


        const tx = await contract.addTick(
            _tokenA,
            _tokenB,
            _price,
            _amount);
        // tx.wait().then(res => {
        //     console.log("res: ", res)
        //     _this.props.history.push('/market')
        // })
        return tx;

    }

    async _initAllowance(res, props) {
        const marketContract = ContractMapping[res.chainId]

        const allowanceBalanceA = await getERC20Allowance(res.provider, props.pair.tokenA, res.address, marketContract)
        const allowanceBalanceB = await getERC20Allowance(res.provider, props.pair.tokenB, res.address, marketContract)

        const balanceA = await getBalance(res.provider, marketContract, props.pair.tokenA, res.address)
        const frozenA = await getFrozen(res.provider, marketContract, props.pair.tokenA, res.address)

        const balanceB = await getBalance(res.provider, marketContract, props.pair.tokenB, res.address)
        const frozenB = await getFrozen(res.provider, marketContract, props.pair.tokenB, res.address)

        this.setState({
            allowanceBalanceA: allowanceBalanceA,
            allowanceBalanceB: allowanceBalanceB,

            balanceOfA: balanceA - frozenA,
            balanceOfB: balanceB - frozenB,
        })
    }

    async onConnect(res, props) {

        const infoA = await getERC20Info(res.provider, props.pair.tokenA, res.address)
        const infoB = await getERC20Info(res.provider, props.pair.tokenB, res.address)

        const marketContract = ContractMapping[res.chainId]

        const contract = new ethers.Contract(marketContract, ABI_TICK, res.provider)
        const platformFee = await contract.getFeeRate()
        const marketFee = await contract.getSubFeeRate(props.pair.tokenA, props.pair.tokenB)

        this._initAllowance(res, props)

        this.setState({
            symbolA: infoA.symbol,
            symbolB: infoB.symbol,
            amountA: infoA.balanceInEther,
            amountB: infoB.balanceInEther,
            platformFee: Number(platformFee),
            marketFee: Number(marketFee),
            decimalsA: infoA.decimals,
            decimalsB: infoB.decimals
        })
    }

    async changeDirection(direction) {
        this.setState({
            direction: direction
        })
        const _this = this
        setTimeout(() => {
            _this.calcuteTotal()
        }, 500);
    }

    async calcuteTotal() {

        if (!this.state.price || !this.state.amount) {
            this.setState({
                total: '-',
                totalPlatformFee: '-',
                totalMarketFee: '-'
            })
            return
        }

        const total = this.state.price * this.state.amount
        const feePlatform = this.state.platformFee / 10000 * total
        const feeMarket = this.state.marketFee / 10000 * total

        const _total = this.state.direction === 'buy' ? total : total - feePlatform - feeMarket
        // const _total = total - feePlatform - feeMarket

        this.setState({
            total: _total,
            totalPlatformFee: feePlatform,
            totalMarketFee: feeMarket
        })
    }

    async onAmountChange(e) {
        this.setState({
            amount: e.target.value
        })
        const _this = this
        setTimeout(() => {
            _this.calcuteTotal()
        }, 500);
    }

    async onPriceChange(e) {
        this.setState({
            price: e.target.value
        })
        const _this = this
        setTimeout(() => {
            _this.calcuteTotal()
        }, 500);
    }

    showLoading() {
        this.setState({
            loading: true
        })
    }

    hideLoading() {
        this.setState({
            loading: false
        })
    }

    doApprove = async (e, tokenContract, amountInUint256) => {

        console.log("doApprove: ", tokenContract, amountInUint256)

        try {
            this.showLoading()
            const res = await connect()
            const provider = res.provider
            const signer = await provider.getSigner()
            const contract = new ethers.Contract(tokenContract, ABI_ERC20, signer)

            // console.log(res.chainId)
            if (res.chainId + '' === '202008201' && tokenContract.toLocaleLowerCase() === '0x950259F74FC297A44bc3725aBa2E9B9028B2A53a'.toLocaleLowerCase() && amountInUint256 > 0) {
                // get the et allowance first
                const etContract = new ethers.Contract('0x950259F74FC297A44bc3725aBa2E9B9028B2A53a', ABI_ERC20, signer)
                const etAllowance = await etContract.allowance(res.address, ContractMapping[res.chainId])
                // if etAllowance > 0 then set amountInUnit256 = 0
                if (etAllowance > 0) {
                    amountInUint256 = 0
                }
            }

            const tx = await contract.approve(ContractMapping[res.chainId], amountInUint256)

            console.log("tx: ", tx)
            await tx.wait(1)
            
            this._initAllowance(res, this.props)
        } catch (e) {
            console.error(e)
            alert(e.reason)
        } finally {
            this.hideLoading()
        }
    }

    render() {
        const infoStyle = {
            padding: '16px',
            marginTop: '16px',
            backgroundColor: '#ffffff',
            borderRadius: '8px',
            textAlign: 'center',
            lineHeight: '2'
            // display: 'flex',
            // justifyContent: 'space-between',
            // alignItems: 'center'
        }

        let error1 = (<div></div>)
        if (this.state.amount && this.state.direction === 'sell' && this.state.allowanceBalanceA - ethers.parseUnits(this.state.amount + '', this.state.decimalsA) - this.state.balanceOfA < 0) {
            error1 = (

                <div style={{ fontSize: '12px', color: 'red' }}>Insufficient allowance

                    <span onClick={(e) => { this.doApprove(e, this.state.tokenA, ethers.parseUnits(this.state.amount + '', this.state.decimalsA)) }} style={{ color: 'blue' }}>[Click to Approve]</span>
                </div>
            )
        } else {
            error1 = (<div></div>)
        }

        let error2 = (<div></div>)
        if (this.state.price && this.state.amount && this.state.direction === 'buy' && this.state.allowanceBalanceB - ethers.parseUnits((this.state.price * this.state.amount) + '', this.state.decimalsB) - this.state.balanceOfB < 0) {
            error2 = (
                <div style={{ fontSize: '12px', color: 'red' }}>Insufficient allowance

                    <span onClick={(e) => { this.doApprove(e, this.state.tokenB, ethers.parseUnits((this.state.price * this.state.amount) + '', this.state.decimalsB)) }} style={{ color: 'blue' }}>[Click to Approve]</span>
                </div>
            )
        } else {
            error2 = (<div></div>)
        }


        return (
            <div >
                <NavBar onConnect={(res) => { this.onConnect(res, this.props) }} tokenA={this.state.tokenA} tokenB={this.state.tokenB} />
                <div style={{ margin: '16px' }}>
                    <center style={{ fontSize: '18px', fontStyle: 'bold' }}>Place an order</center>
                    {/* <div style={infoStyle}>
                        <div>{this.state.symbolA} ({this.props.pair.tokenA.replace(/^(.{6}).+(.{4})$/, "$1...$2")}) </div>
                        <div style={{fontSize: '12px'}}>{this.state.amountA}</div>
                    </div>
                    <center style={{marginTop: '16px'}}>
                        <img style={{width: '20px', height: '20px'}} src={require('../assets/exchange.png')}></img>
                    </center>
                    <div style={infoStyle}>
                        <div>{this.state.symbolB} ({this.props.pair.tokenB.replace(/^(.{6}).+(.{4})$/, "$1...$2")}) </div>
                        <div style={{fontSize: '12px'}}>{this.state.amountB}</div>
                    </div> */}

                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                        <div style={infoStyle}>
                            <div>{this.state.symbolA}</div>
                            <div style={{ fontSize: '12px' }}>({this.props.pair.tokenA.replace(/^(.{6}).+(.{4})$/, "$1...$2")})</div>
                        </div>
                        <div style={{ margin: '16px' }}>
                            <img alt='' style={{ width: '28px', height: '28px' }} src={require('../assets/exchange2.png')}></img>
                        </div>
                        <div style={infoStyle}>
                            <div>{this.state.symbolB} </div>
                            <div style={{ fontSize: '12px' }}>({this.props.pair.tokenB.replace(/^(.{6}).+(.{4})$/, "$1...$2")})</div>
                        </div>
                    </div>

                    <div style={{ marginTop: '16px', fontSize: '12px' }}>
                        Direction
                    </div>
                    <div style={{ marginTop: '5px', display: 'flex', alignItems: 'center' }}>
                        <div
                            onClick={() => { this.changeDirection('buy') }}
                            style={{ padding: '12px', borderRadius: '8px 0 0 8px', backgroundColor: this.state.direction === 'buy' ? '#0b57ff' : '#CCCCCC', width: '50%', textAlign: 'center', color: this.state.direction === 'buy' ? '#ffffff' : '#000000' }}
                        >Buy</div>
                        <div
                            onClick={() => { this.changeDirection('sell') }}
                            style={{ padding: '12px', borderRadius: '0 8px 8px 0', backgroundColor: this.state.direction !== 'buy' ? '#0b57ff' : '#CCCCCC', width: '50%', textAlign: 'center', color: this.state.direction !== 'buy' ? '#ffffff' : '#000000' }}
                        >Sell</div>
                    </div>

                    <div style={{ marginTop: '16px', fontSize: '12px', display: 'flex', justifyContent: 'space-between' }}>
                        <div>Amount</div>
                        <div>
                            <span style={{ fontSize: '12px', color: '#999999' }}>Balance: {this.state.amountA} {this.state.symbolA} </span>
                        </div>
                    </div>

                    <div style={{ marginTop: '5px', display: 'flex', alignItems: 'center' }}>
                        <input onChange={(e) => this.onAmountChange(e)} value={this.state.amount} style={{ width: '100%', padding: '12px', borderRadius: '8px', }} placeholder="Amount" type="number"></input>
                        <div
                            style={{ padding: '12px', borderRadius: '0 8px 8px 0', backgroundColor: '#CCCCCC' }}
                        >{this.state.symbolA}</div>
                    </div>
                    <div style={{ marginTop: '5px' }}>
                        <div style={{ fontSize: '12px', color: '#999999' }}>Available balance in market: {ethers.formatUnits(this.state.balanceOfA, this.state.decimalsA)} {this.state.symbolA}</div>
                        <div style={{ fontSize: '12px', color: '#999999' }}>Allowance quotas: {ethers.formatUnits(this.state.allowanceBalanceA, this.state.decimalsA)} {this.state.symbolA} </div>
                        {error1}
                    </div>

                    <div style={{ marginTop: '16px', fontSize: '12px', display: 'flex', justifyContent: 'space-between' }}>
                        <div>Price</div>
                        <div>
                            <span style={{ fontSize: '12px', color: '#999999' }}>Balance: {this.state.amountB} {this.state.symbolB} </span>
                        </div>
                    </div>
                    <div style={{ marginTop: '5px', display: 'flex', alignItems: 'center' }}>
                        <input onChange={(e) => { this.onPriceChange(e) }} value={this.state.price} style={{ width: '100%', padding: '12px', borderRadius: '8px', }} placeholder="Price" type="number"></input>
                        <div
                            style={{ padding: '12px', borderRadius: '0 8px 8px 0', backgroundColor: '#CCCCCC' }}
                        >{this.state.symbolB}</div>
                    </div>
                    <div style={{ marginTop: '5px' }}>
                        <div style={{ fontSize: '12px', color: '#999999' }}>Available balance in market: {ethers.formatUnits(this.state.balanceOfB, this.state.decimalsB)}  {this.state.symbolB}</div>
                        <div style={{ fontSize: '12px', color: '#999999' }}>Allowance quotas: {ethers.formatUnits(this.state.allowanceBalanceB, this.state.decimalsB)} {this.state.symbolB}</div>
                        {error2}
                    </div>

                    {/* the market fee and the platform fee display */}
                    <div style={{ marginTop: '16px', fontSize: '12px' }}>
                        Fee rate
                    </div>
                    <div style={{ marginTop: '5px', fontSize: '12px' }}>
                        <div>
                            <i>Platform fee: {this.state.totalPlatformFee} {this.state.direction === 'buy' ? this.state.symbolB : this.state.symbolA}, {this.state.platformFee / 10000 * 100}% </i>
                        </div>
                        <div>
                            <i>Market fee: {this.state.totalMarketFee} {this.state.direction === 'buy' ? this.state.symbolB : this.state.symbolA}, {this.state.marketFee / 10000 * 100}% </i>
                        </div>
                    </div>
                    {/** the totle token B required or received */}
                    <div style={{ marginTop: '16px', fontSize: '12px' }}>
                        {this.state.direction === 'buy' ? 'Total required' : 'Will received'}
                    </div>
                    <div style={{ marginTop: '5px', fontSize: '16px', fontStyle: 'bold' }}>
                        <div>
                            <i>{this.state.total} {this.state.symbolB}</i>
                        </div>
                    </div>

                    {/** button to place the order */}
                    <div style={{ marginTop: '16px', fontSize: '16px' }}>
                        <button onClick={this.doAddTick} style={{ padding: '16px', borderRadius: '8px', backgroundColor: '#0b57ff', color: '#ffffff', width: '100%' }}>Place Order</button>
                    </div>
                </div>
                <TLoading show={this.state.loading} />
            </div>
        );
    }
}

function MakeTickPage() {
    const [params] = useSearchParams();
    const navigate = useNavigate();

    const pair = {
        tokenA: params.get("tokenA"),
        tokenB: params.get("tokenB"),
        // get the direction from params, set default is 'buy'
        direciton: params.get("direction") || 'buy'
    }

    return (
        <MakeTick pair={pair} onBidSuccess={
            (res) => {
                
                navigate('/result', { state: res })
            }
        } />

    );
}

export default MakeTickPage;