import axios from 'axios';
export const BASE_URL = 'https://otcapi.metadragon.ai'



// post to api from parameters
export const post = (url, params) => {
    return new Promise((resolve, reject) => {
        axios.post(BASE_URL + url, params)
            .then(response => {
                resolve(response.data);
            })
            .catch(error => {
                reject(error);
            });
    });
};

// get from api use axios   
export const get = (url, params) => {
    return new Promise((resolve, reject) => {
        // axios.get(BASE_URL + url, params)
        //     .then(response => {
        //         resolve(response.data);
        //     })
        //     .catch(error => {
        //         reject(error);
        //     });

        axios({
            method: 'get',
            url: BASE_URL + url,
            params: params
        }).then(response => {
            resolve(response.data);
        }
        ).catch(error => {
            reject(error);
        }
        );
    });
};

